import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import web3 from './web3';
import crowdfunding from './crowdfunding';

class App extends Component {
  state = {
    owner: '',
    master: '',
    entrepreneur: '',
    title: '' ,
    activeCampaigns: [],
    completedCampaigns: [],
    cancelledCampaigns: [], 
    numPledges: '', 
	  balance: '',
	  fees: '',
    message: '',
    newAddress: '', // when changing owner
    banAddress: '', // when someone is banned
    isBanned: false, // to check if the currentAccount is banned
    isDestructed: false, // to check if contract is destroyed
    currentAccount: ''
  }; 

  async componentDidMount() {
    try {
    const owner = await crowdfunding.methods.getOwner().call();
    const master = await crowdfunding.methods.getMaster().call();
      /*const currentAccount = (await window.ethereum.request({ method: 'eth_requestAccounts' }))[0];*/
	  const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
	  const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');

    const isDestructed = await crowdfunding.methods.getContractState().call();
    
    const campaigns = await crowdfunding.methods.getActiveCampaigns().call();
    const cancelCampaigns = await crowdfunding.methods.getCancelledCampaigns().call();
    const completeCampaigns = await crowdfunding.methods.getCompletedCampaigns().call();


    // Convert the campaigns object into proper arrays
    const ids = campaigns[0]; // Campaign IDs
    const entrepreneurs = campaigns[1]; // Entrepreneurs
    const titles = campaigns[2]; // Titles
    const pledgeCosts = campaigns[3]; // Pledge costs
    const pledgesNeeded = campaigns[4]; // Total pledges needed
    const pledgesCount = campaigns[5]; // Current pledge counts

    
     //Map the arrays into an array of campaign objects
    const activeCampaigns = ids.map((id, index) => ({
      id: id.toString(), // Convert BigInt to string
      entrepreneur: entrepreneurs[index],
      title: titles[index],
      price: web3.utils.fromWei(pledgeCosts[index], 'ether'), // Convert BigInt to string, then Wei to ETH
      left: (pledgesNeeded[index] - pledgesCount[index]).toString(), // Remaining pledges (BigInt subtraction)
      backers: 0, // Will get value later (need to call function)
      yourPledges: 0 // Will get value later (need to call function)
    }));

    //COMPLETED CAMPAIGNS
    // Convert the campaigns object into proper arrays
    const completeids = completeCampaigns[0]; // Campaign IDs
    const completeentrepreneurs = completeCampaigns[1]; // Entrepreneurs
    const completetitles = completeCampaigns[2]; // Titles
    const completepledgeCosts = completeCampaigns[3]; // Pledge costs
    const completepledgesNeeded = completeCampaigns[4]; // Total pledges needed
    const completepledgesCount = completeCampaigns[5]; // Current pledge counts

    
     //Map the arrays into an array of campaign objects
    const completedCampaigns = completeids.map((id, index) => ({
      id: id.toString(), // Convert BigInt to string
      entrepreneur:completeentrepreneurs[index],
      title: completetitles[index],
      price: web3.utils.fromWei(completepledgeCosts[index], 'ether'), // Convert BigInt to string, then Wei to ETH
      left: (completepledgesNeeded[index] - completepledgesCount[index]).toString(), // Remaining pledges (BigInt subtraction)
      backers: 0, // Will get value later (need to call function)
      yourPledges: 0 // Will get value later (need to call function)
    }));
    


    //CANCELLED CAMPAIGNS
    // Convert the campaigns object into proper arrays
    const cancelids = cancelCampaigns[0]; // Campaign IDs
    const cancelentrepreneurs = cancelCampaigns[1]; // Entrepreneurs
    const canceltitles = cancelCampaigns[2]; // Titles
    const cancelpledgeCosts = cancelCampaigns[3]; // Pledge costs
    const cancelpledgesNeeded = cancelCampaigns[4]; // Total pledges needed
    const cancelpledgesCount = cancelCampaigns[5]; // Current pledge counts

    
     //Map the arrays into an array of campaign objects
    const cancelledCampaigns = cancelids.map((id, index) => ({
      id: id.toString(), // Convert BigInt to string
      entrepreneur: cancelentrepreneurs[index],
      title: canceltitles[index],
      price: web3.utils.fromWei(cancelpledgeCosts[index], 'ether'), // Convert BigInt to string, then Wei to ETH
      left: (cancelpledgesNeeded[index] - cancelpledgesCount[index]).toString(), // Remaining pledges (BigInt subtraction)
      backers: 0, // Will get value later (need to call function)
      yourPledges: 0 // Will get value later (need to call function)
    }));
 
   
    this.setState({ owner, master, balance, fees, isDestructed, activeCampaigns, completedCampaigns, cancelledCampaigns});
    try { // Metamask
      const addr = (await window.ethereum.request({ method: 'eth_requestAccounts' }))[0]; //case-insensitive
      const currentAccount = web3.utils.toChecksumAddress(addr); // case-sensitive

      const isBanned = await this.checkIfBanned(currentAccount);

      this.setState({message: '', currentAccount });
    } catch (error) { // If metamask denied the request
      this.setState({ message: 'Metamask has not connected yet' });
    }
    
    // Fetch dynamic campaign data (backers and pledges) synchronously
    for (let i = 0; i < activeCampaigns.length; i++) {
        const id = ids[i];
        const backers = await crowdfunding.methods.getNumOfBackers(id).call();
        const yourPledges = await crowdfunding.methods.getBackersPledges(id, this.state.currentAccount).call();
    
        // Update the specific campaign with the fetched data
        activeCampaigns[i].backers = backers.toString();
        activeCampaigns[i].yourPledges = yourPledges.toString();
    }

    // FOR COMPLETED
    // Fetch dynamic campaign data (backers and pledges) synchronously
    for (let i = 0; i < completedCampaigns.length; i++) {
      const id = completeids[i];
      const backers = await crowdfunding.methods.getNumOfBackers(id).call();
      const yourPledges = await crowdfunding.methods.getBackersPledges(id, this.state.currentAccount).call();
  
      // Update the specific campaign with the fetched data
      completedCampaigns[i].backers = backers.toString();
      completedCampaigns[i].yourPledges = yourPledges.toString();
  }
  
    
    // FOR CANCELLED
    // Fetch dynamic campaign data (backers and pledges) synchronously
    for (let i = 0; i < cancelledCampaigns.length; i++) {
        const id = cancelids[i];
        const backers = await crowdfunding.methods.getNumOfBackers(id).call();
        const yourPledges = await crowdfunding.methods.getBackersPledges(id, this.state.currentAccount).call();
    
        // Update the specific campaign with the fetched data
        cancelledCampaigns[i].backers = backers.toString();
        cancelledCampaigns[i].yourPledges = yourPledges.toString();
    }
    
    this.setState({ activeCampaigns, completedCampaigns, cancelledCampaigns });
 
    } catch (error) {
      console.log('Error fetching campaigns or connecting wallet', error);
    }
	
	// Set up event listeners
      if (!this.eventListenersSet) {
        this.setupEventListeners();
        this.eventListenersSet = true;
        console.log("Event listeners initiated");
      }
  }

  //FOR COMPLETED
  
  setupEventListeners() {
    if (this.eventListenersSet) return;

		// Refresh each time a wallet on metamask changes
		window.ethereum.on('accountsChanged', async (accounts) => {
			const currentAccount = web3.utils.toChecksumAddress(accounts[0]); //use this to convert case-insensitive addr to case-sensitive

      const isBanned = await this.checkIfBanned(currentAccount);

      this.setState({ currentAccount, isBanned });

      // Fetch the updated pledges for the new wallet
      const { activeCampaigns, cancelledCampaigns, completedCampaigns } = this.state;

      for (let i = 0; i < activeCampaigns.length; i++) {
        const campaign = activeCampaigns[i];
        const id = await crowdfunding.methods.fetchID(activeCampaigns[i].title).call();

        // Fetch updated backers and yourPledges data
        const backers = await crowdfunding.methods.getNumOfBackers(id).call();
        const yourPledges = await crowdfunding.methods.getBackersPledges(id,currentAccount).call();

        // Update the campaign with the fetched data
        campaign.backers = backers.toString();
        campaign.yourPledges = yourPledges.toString();
      }

      for (let i = 0; i < cancelledCampaigns.length; i++) {
        const campaign = cancelledCampaigns[i];
        const id = await crowdfunding.methods.fetchID(cancelledCampaigns[i].title).call();

        // Fetch updated backers and yourPledges data
        const backers = await crowdfunding.methods.getNumOfBackers(id).call();
        const yourPledges = await crowdfunding.methods.getBackersPledges(id,currentAccount).call();

        // Update the campaign with the fetched data
        campaign.backers = backers.toString();
        campaign.yourPledges = yourPledges.toString();
      }

      for (let i = 0; i < completedCampaigns.length; i++) {
        const campaign = completedCampaigns[i];
        const id = await crowdfunding.methods.fetchID(completedCampaigns[i].title).call();

        // Fetch updated backers and yourPledges data
        const backers = await crowdfunding.methods.getNumOfBackers(id).call();
        const yourPledges = await crowdfunding.methods.getBackersPledges(id,currentAccount).call();

        // Update the campaign with the fetched data
        campaign.backers = backers.toString();
        campaign.yourPledges = yourPledges.toString();
      }

			this.setState({ activeCampaigns, cancelledCampaigns, currentAccount });
		});

    crowdfunding.events.CampaignCreated().on('data', async (data) => {
      const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
      const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');
      
      const campaignID = data.returnValues.title;

      const newCampaign = {
        entrepreneur: data.returnValues.entrepreneur, // Account creating the campaign
        title: data.returnValues.title, 
        price: web3.utils.fromWei(data.returnValues.pledgePrice, 'ether'), 
        backers: 0, // Initially, no backers
        left: data.returnValues.pledgesLeft.toString(), 
        yourPledges: 0, // Initially, no pledges 
      };


      console.log("Event listener CampaignCreated called");
      this.setState((state) => ({
        balance,
        fees,
        activeCampaigns: [...state.activeCampaigns, newCampaign], // Append the new campaign
      }));
      });

      
      crowdfunding.events.BackerRefunded().on('data', async (data) => {
        const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
        const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');

        const campaignID = data.returnValues.title;
        
        this.setState((state) => ({
        cancelledCampaigns: state.cancelledCampaigns.map((campaign) =>
          campaign.title === campaignID
            ? { ...campaign, yourPledges: 0 } // Update `yourPledges` to 0, since backer was refunded
            : campaign // Keep other campaigns unchanged
          ),
        }));

        this.setState({balance, fees}); // Update the contract's balance
        });

      crowdfunding.events.CampaignCancelled().on('data', async (data) => {
        const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
        const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');

        const campaignID = data.returnValues.title;
        
        const { activeCampaigns, cancelledCampaigns }= this.state;
        const campaignIndex = activeCampaigns.findIndex((campaign) => campaign.id === campaignID.toString());
  
        const cancelledCampaign = activeCampaigns.splice(campaignIndex, 1)[0]; // Remove from active and add to cancelled
        cancelledCampaigns.push({
          ...cancelledCampaign
        });

        console.log("Event listener CampaignCancelled called");
        this.setState({balance, fees, activeCampaigns, cancelledCampaigns});
        });      

      crowdfunding.events.CampaignCompleted().on('data', async (data) => {
        const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
        const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');

        const campaignID = data.returnValues.title;
        
        const { activeCampaigns, completedCampaigns }= this.state;
        const campaignIndex = activeCampaigns.findIndex((campaign) => campaign.id === campaignID.toString());
  
        const completedCampaign = activeCampaigns.splice(campaignIndex, 1)[0]; // Remove from active and add to fulfilled
        completedCampaigns.push({
          ...completedCampaign
        });

        this.setState({balance, fees, activeCampaigns, completedCampaigns});
        });   

    crowdfunding.events.PledgeMade().on('data', async (data) => {
      const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
      const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');

      const campaigns = await crowdfunding.methods.getActiveCampaigns().call();


      // Convert the campaigns object into proper arrays
      const ids = campaigns[0]; // Campaign IDs
      const entrepreneurs = campaigns[1]; // Entrepreneurs
      const titles = campaigns[2]; // Titles
      const pledgeCosts = campaigns[3]; // Pledge costs
      const pledgesNeeded = campaigns[4]; // Total pledges needed
      const pledgesCount = campaigns[5]; // Current pledge counts
  
      
       //Map the arrays into an array of campaign objects
      const activeCampaigns = ids.map((id, index) => ({
        id: id.toString(), // Convert BigInt to string
        entrepreneur: entrepreneurs[index],
        title: titles[index],
        price: web3.utils.fromWei(pledgeCosts[index], 'ether'), // Convert BigInt to string, then Wei to ETH
        left: (pledgesNeeded[index] - pledgesCount[index]).toString(), // Remaining pledges (BigInt subtraction)
        backers: 0, // Will get value later (need to call function)
        yourPledges: 0 // Will get value later (need to call function)
      }));

       // Fetch dynamic campaign data (backers and pledges) synchronously
      for (let i = 0; i < activeCampaigns.length; i++) {
        const id = ids[i];
        const backers = await crowdfunding.methods.getNumOfBackers(id).call();
        const yourPledges = await crowdfunding.methods.getBackersPledges(id, this.state.currentAccount).call();
    
        // Update the specific campaign with the fetched data
        activeCampaigns[i].backers = backers.toString();
        activeCampaigns[i].yourPledges = yourPledges.toString();
      }
  
  console.log("Event listener PledgeMade called");
  this.setState({ activeCampaigns, balance, fees });
   
     
    })

    crowdfunding.events.UserBanned().on('data', async(data) => { // In case a user is banned, change the variable also
      const { currentAccount } = this.state;
      const isBanned = await crowdfunding.methods.isBanned(currentAccount).call();
      this.setState({ isBanned });
    });

    crowdfunding.events.OwnerChanged().on('data', async(data) => { // In case the owner changes, change the variable also
      const owner = await crowdfunding.methods.getOwner().call();
      this.setState({ owner });
    });

    crowdfunding.events.OwnerPayed().on('data', async(data) => { // In case the owner changes, change the variable also
      const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
      this.setState({ balance });
    });

    crowdfunding.events.ContractDestroyed().on('data', async(data) => { // In case the owner changes, change the variable also
      const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
      const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');
      const isDestructed = await crowdfunding.methods.getContractState().call();

      this.setState({ balance, fees, isDestructed });
    });

      /* FOR DEBUGGING PURPOSES */
      const listenerCount = crowdfunding.events.CampaignCreated().listenerCount('data');
      console.log(`Number of CampaignCreated listeners: ${listenerCount}`);

    // Mark listeners as added
  this.eventListenersAdded = true;
      
  }

    checkIfBanned = async (currentAccount) => {
      try {
        const bannedStatus = await crowdfunding.methods.isBanned(currentAccount).call();
        return bannedStatus;
      } catch (error) {
        console.error("Error checking banned status:", error);
      
      }
    };

    // Function to handle campaign creation
    onCreateCampaign = async () => {
      const { campaignId, value, numPledges, currentAccount } = this.state;
  
      if (!campaignId || !value || !numPledges) {
        console.log("Please fill in all the fields");
        return;
      }
  
      try {
        console.log("Creating campaign...");

        const fee = web3.utils.toWei('0.02', 'ether'); // Each time a campaign is created, a fee (0.02 ether) must be paid
        
        await crowdfunding.methods
          .addCampaign(campaignId, web3.utils.toWei(value, 'ether'), numPledges)
          .send({  gas: 200000, gasPrice: 1000000000, from: currentAccount, value: fee });  
        
  
        console.log("Campaign created successfully!");
      } catch (err) {
        console.error(err);
        console.log("Error creating campaign");
      }
    };

    onPledge = async (campaignTitle, price) => {
      const { currentAccount } = this.state;
  
      try {
        console.log("Pledging...");

        const val = web3.utils.toWei(price.toString(), 'ether'); //the amount needed to buy the pledge

        const campaignID = await crowdfunding.methods.fetchID(campaignTitle).call();

        console.log("In campaign with ID: " + campaignID);

        // Call the smart contract method
        await crowdfunding.methods
          .addPledge(campaignID, 1)
          .send({ gas: 200000, gasPrice: 1000000000, from: currentAccount, value: val });
  
        console.log("Pledged successfully!");
      } catch (err) {
        console.error(err);
        console.log("Error pledging");
      }
    };

    onCancel = async (campaignTitle) => {

      try{
        console.log("Cancelling campaign...");
        const {currentAccount} = this.state;

        const campaignID =  await crowdfunding.methods.fetchID(campaignTitle).call();

        console.log("ID of campaign that is to be cancelled: " + campaignID);


        await crowdfunding.methods
          .cancelCampaign(campaignID)
          .send({from: currentAccount});

        console.log("Cancelled campaign successfuly!");
      } catch(err){
        console.error(err);
        console.log("Error cancelling campaign");
      }
    }

    onFulfill = async (campaignTitle) => {

      try{
        console.log("Completing campaign...");
        const {currentAccount} = this.state;

        const campaignID =  await crowdfunding.methods.fetchID(campaignTitle).call();

        console.log("with ID: " + campaignID);

        await crowdfunding.methods
          .completeCampaign(campaignID)
          .send({from: currentAccount});

        console.log("Campaign completed successfuly!");
      } catch(err){
        console.error(err);
        console.log("Error completing campaign");
      }
    }

    // Function for owner to withdraw his amount
    onWithdraw = async () => {
      const {currentAccount } = this.state;
  
      try {
        console.log("Withdrawing owner's portion..."); 

        await crowdfunding.methods
          .payOwner()
          .send({ from: currentAccount });

        console.log("Withdrawal successfull!");
      } catch (err) {
        console.error(err);
        console.log("Error withdrawing");
      }
    };

    onClaim = async() => {
      const { currentAccount } = this.state
      try{
        console.log("Requesting refund...");
        
        await crowdfunding.methods.refundBacker().send({from: currentAccount});
        
        console.log("Refund successful!");  
      }catch(err){
        console.error(err);
        console.log("Error refunding");
      }
    }

    // Function for owner to change the contract's owner
    onChangeOwner = async () => {
      const {currentAccount, newAddress } = this.state;
  
      try {
        console.log("Changing contract owner..."); 
        //console.log(web3.utils.toChecksumAddress(newAddress));
        await crowdfunding.methods
          .changeOwner(web3.utils.toChecksumAddress(newAddress))
          .send({ from: currentAccount });
      
        console.log("Change successfull!");
      } catch (err) {
        console.error(err);
        console.log("Error changing owner");
      }
    };

    onBan = async() => {
      const { currentAccount, banAddress } = this.state;

      try{
        console.log("Trying to ban user...");

        await crowdfunding.methods
          .addToBannedList(banAddress)
          .send({ from: currentAccount });

        console.log("User banned successfully");
      
      }catch (err){
        console.error(err);
        console.log("Error trying to ban user");
      }
    }

    onDestroy = async() => {
      const {currentAccount, isDesturcted, balance, fees } = this.state;

      try{
        console.log("Trying to destruct contract...");

        await crowdfunding.methods
          .destroySmartContract()
          .send({ from:currentAccount });

        const isDestructed = await crowdfunding.methods.getContractState().call();
        const balance = web3.utils.fromWei(await crowdfunding.methods.getBalance().call(), 'ether');
        const fees = web3.utils.fromWei(await crowdfunding.methods.getFees().call(), 'ether');

        console.log("Contract destroyed successfully");

        this.setState(isDestructed, balance, fees);
    
      }catch (err){
        console.error(err);
        console.log("Error destroying contract");
      }
    }


    

  render() {
    return (
      <div className="container">
		{/* Addreses Section*/}
        <h2 className="my-4">Crowdfunding DApp</h2>
		
		<div className="d-flex mb-3 ">
		<strong className="me-3">Current Address: </strong> 	 
		<input
              className="form-control"
              type="text"
              placeholder={this.state.currentAccount}
			  disabled = "disabled"
              style={{ width: '400px' }}
            /> <br />
        </div>
		
		<div className="d-flex mb-3">
          <strong className="me-3">Owner's Address: </strong> 
		  <input
              className="form-control"
              type="text"
              placeholder={this.state.owner}
			  disabled = "disabled"
              style={{ width: '400px' }}
            /> <br />
        </div>
		
		<div className="d-flex align-item-center">
          <strong className="me-3">Balance </strong> 
		  <input
              className="form-control"
              type="text"
              placeholder={this.state.balance}
			  disabled = "disabled"
              style={{ width: '80px' }}
            /> 
			
		  <strong className="ms-3 me-3">Colected fees </strong> 
		  <input
              className="form-control"
              type="text"
              placeholder={this.state.fees}
			  disabled = "disabled"
              style={{ width: '80px' }}
            />
			
			<br />
        </div>
      

		<hr /> 
	
        {/*New Campaign Section */}
        <div className="mb-4">
          <h4>New Campaign</h4>
          <div className="mb-4">
          <div className="d-flex align-items-center mb-2">
            <label className="me-2" style={{ width: '120px' }}>Title</label>
            <input
              className="form-control"
              type="text"
              placeholder="Campaign's ID"
              onChange={(e) => this.setState({ campaignId: e.target.value })}
              style={{ width: '300px' }}
            />
          </div>
        </div>
          <div className="d-flex align-items-center mb-2">
            <label className="me-2" style={{ width: '120px' }}>Pledge cost</label>
            <input
              className="form-control"
              type="number"
              placeholder="e.g. 0.01"
              onChange={(e) => this.setState({ value: e.target.value })}
              style={{ width: '300px' }}
            />
          </div>
          <div className="d-flex align-items-center mb-3">
            <label className="me-2" style={{ width: '120px' }}>Number of pledges</label>
            <input
              className="form-control"
              type="number"
              placeholder="e.g. 200"
              onChange={(e) => this.setState({ numPledges: e.target.value })}
              style={{ width: '300px' }}
            />
          </div>
          <button type="submit" 
          className="btn btn-primary" 
          onClick={this.onCreateCampaign}
          disabled={this.state.isDestructed || this.state.isBanned || this.state.currentAccount === this.state.owner || this.state.currentAccount === this.state.master}>Create</button>
        </div>
		
		<hr /> 

        {/* Live Campaigns Section */}
        <h4>Live campaigns</h4>
        <div className="mb-4">  
          <span style={{ width: '150px', marginRight: '300px' }}> Entrepreneur</span>
          <span style={{ width: '150px', marginRight: '100px' }}> Title</span>
          <span style={{ width: '150px', marginRight: '100px' }}> Price / Backers / Pledges left / Your Pledges</span>
          {this.state.activeCampaigns.map((campaign, index) => (
            <div key={index} className="d-flex align-items-center mb-2">
              <span style={{ width: '300px', marginRight: '80px' }}>{campaign.entrepreneur}</span>
              <span style={{ width: '150px', marginRight: '100px' }}>{campaign.title}</span>
              <span style={{ width: '250px'}}>
                {campaign.price} / {campaign.backers} / {campaign.left} / {campaign.yourPledges}
              </span>
              <button className="btn btn-success btn-sm me-2"
                      onClick={() => this.onPledge(campaign.title, campaign.price)}>Pledge</button> {/* Passing campaignID and price to the onPledge function */}
              <button className="btn btn-danger btn-sm me-2"
                       onClick = {() => this.onCancel(campaign.title)} 
                       hidden = {this.state.currentAccount !== campaign.entrepreneur && this.state.currentAccount !== this.state.owner && this.state.currentAccount !== this.state.master}>Cancel</button>
              <button className="btn btn-secondary btn-sm"
                        style={{ backgroundColor: 'orange' }}
                        onClick={() => this.onFulfill(campaign.title)}
                        hidden = {this.state.currentAccount !== campaign.entrepreneur && this.state.currentAccount !== this.state.owner && this.state.currentAccount !== this.state.master}
                        disabled = {campaign.left > 0}>Fulfill</button>
            </div>
          ))}
        </div>

		
		<hr /> 

        {/* Fulfilled Campaigns Section */}
        <h4>Fulfilled campaigns</h4>
        <div className="mb-4">  
          <span style={{ width: '150px', marginRight: '300px' }}> Entrepreneur</span>
          <span style={{ width: '150px', marginRight: '100px' }}> Title</span>
          <span style={{ width: '150px', marginRight: '100px' }}> Price / Backers / Pledges left / Your Pledges</span>
          {this.state.completedCampaigns.map((campaign, index) => (
            <div key={index} className="d-flex align-items-center mb-2">
              <span style={{ width: '300px', marginRight: '80px' }}>{campaign.entrepreneur}</span>
              <span style={{ width: '150px', marginRight: '100px' }}>{campaign.title}</span>
              <span style={{ width: '250px'}}>
                {campaign.price} / {campaign.backers} / {campaign.left} / {campaign.yourPledges}
              </span>
            </div>
          ))}
        </div>
		
		<hr /> 


        {/* Cancelled Campaigns Section */}
        <h4 className="d-inline-block" style={{marginRight: '40px'}}>Canceled campaigns</h4>
        <button className="btn btn-secondary btn-sm "
            onClick = {this.onClaim}  
            disabled={!this.state.cancelledCampaigns.some(campaign => campaign.yourPledges > 0)}>Claim</button>
        <div className="mb-4">  
          <span style={{ width: '150px', marginRight: '300px' }}> Entrepreneur</span>
          <span style={{ width: '150px', marginRight: '100px' }}> Title</span>
          <span style={{ width: '150px', marginRight: '100px' }}> Price / Backers / Pledges left / Your Pledges</span>
          {this.state.cancelledCampaigns.map((campaign, index) => (
            <div key={index} className="d-flex align-items-center mb-2">
              <span style={{ width: '300px', marginRight: '80px' }}>{campaign.entrepreneur}</span>
              <span style={{ width: '150px', marginRight: '100px' }}>{campaign.title}</span>
              <span style={{ width: '250px'}}>
                {campaign.price} / {campaign.backers} / {campaign.left} / {campaign.yourPledges}
              </span>
          
            </div>
          ))}
        </div>

		
		<hr /> 

        {/* Control Panel */}
 <h4>Control Panel</h4>
        {/* Withdraw */}
        <div className="mb-4">
          <button 
          className="btn btn-light"
          onClick = {this.onWithdraw}
          style={{ backgroundColor: '#d3d3d3', color: 'black', border: '1px solid #bbb', width: '100px' }}
          disabled={this.state.isDestructed || (this.state.currentAccount !== this.state.owner && this.state.currentAccount !== this.state.master)}>Withdraw</button>
		  
		      <br/>
		      <br/>

          {/* Change Owner */}
          <div className="d-flex align-items-left">
			    <button 
          className="btn btn-light me-4"
          onClick = {this.onChangeOwner}
          style={{ backgroundColor: '#d3d3d3', color: 'black', border: '1px solid #bbb', width: '100px' }}
          disabled={this.state.isDestructed || (this.state.currentAccount !== this.state.owner && this.state.currentAccount !== this.state.master)}>Change</button>
            <input
              className="form-control me-2"
              type="text"
              placeholder="Enter new owner's wallet address"
              onChange={(e) => this.setState({ newAddress: e.target.value })}
              style={{ width: '400px' }}
            />
            
          </div>
		  
		      <br/>

          {/* Ban Entrepreneur */}
          <div className="d-flex align-items-left">
            <button 
            className="btn btn-light me-4"
            onClick = {this.onBan}
            style={{ backgroundColor: '#d3d3d3', color: 'black', border: '1px solid #bbb', width: '100px' }}
            disabled={this.state.isDestructed || (this.state.currentAccount !== this.state.owner && this.state.currentAccount !== this.state.master)}>Ban</button>
            <input
              className="form-control me-2"
              type="text"
              placeholder="Enter entrepreneur's address"
              onChange={(e) => this.setState({ banAddress: e.target.value })}
              style={{ width: '400px' }}
            />

        

          
            
          </div>

          <br/>

          {/* Destroy Contract */}
          <button 
          className="btn btn-light"
          onClick = {this.onDestroy}
          style={{ backgroundColor: '#d3d3d3', color: 'black', border: '1px solid #bbb', width: '100px' }}
          disabled={this.state.isDestructed || (this.state.currentAccount !== this.state.owner && this.state.currentAccount !== this.state.master)}>Destroy</button>
        </div>
      </div>
    );
  }
}

export default App;